import Vue from 'vue';
import  VueRouter from 'vue-router';

Vue.use(VueRouter);

import Homepage from './pages/home/homepage.vue';
import Chat from './pages/home/chat.vue';
import Dynamic from './pages/home/dynamic.vue';
import My from './pages/home/my.vue';
import Issue from './pages/home/issue.vue';

export default new VueRouter({
    routes: [
        {
            path: '/',
            component: Homepage
        },
        {
            path: '/chat',
            component: Chat
        },
        {
            path: '/issue',
            component: Issue
        },
        {
            path: '/dynamic',
            component: Dynamic
        },
        {
            path: '/my',
            component: My
        }
    ]
    
});